import React, { useState, useLayoutEffect } from 'react';
import './Presentation.css';
import { Typography, Grow, Hidden } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ArrowBackOutlined from '@material-ui/icons/ArrowBackOutlined'
import Const from '../helpers/Const'
import About from './About'
import Menu from './Menu'
import Locales from './Locales'

const useStyles = makeStyles(theme => ({
  titulo: {
    fontWeight: 400,
    fontFamily: "Cinzel, serif"//"Nothing You Could Do"
  },
  lightFont: {
    fontWeight: 300,
  }
}));

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);
  return size;
}

function Presentation({ language, setLanguage }) {
  const classes = useStyles();
  const [mouseX, setMouseX] = useState(0)
  const [mouseY, setMouseY] = useState(0)
  const [section, setSection] = useState('index')
  const [shouldBackdropNav, setShouldBackdropNav] = useState(false)
  const [width, height] = useWindowSize();
  const isImgRotable = width > 960 && section === 'index'
  const labels = Const.labels[language]
  const topBarBackgroundThreshold = 30

  const handleSectionChange = (section) => {
    setSection(section)
    setShouldBackdropNav(false)
  }

  const handleScroll = e => {
    let element = e.target
    if (!shouldBackdropNav) {
      if (element.scrollTop > topBarBackgroundThreshold) {
        setShouldBackdropNav(true)
      }
    } else if (element.scrollTop < topBarBackgroundThreshold) {
      setShouldBackdropNav(false)
    }
  }

  const onMouseMove = (event) => {
    if (isImgRotable) {
      setMouseX(event.screenX)
      setMouseY(event.screenY)
    }
  }

  const renderMenuOptions = (isIndex) => {
    return (
      <div className={'sections-buttons-container'+(isIndex? ' sections-buttons-container-center' : '')}>
        <button onClick={() => section === 'nosotros' ? handleSectionChange('index') : handleSectionChange('nosotros')} className={"button" + (section === "nosotros" ? ' button-selected' : '')}> <Typography variant='h6' className={classes.lightFont} >{labels.sectionTitles.nosotros.toUpperCase()}</Typography></button>
        <button onClick={() => section === 'menu' ? handleSectionChange('index') : handleSectionChange('menu')} className={"button" + (section === "menu" ? ' button-selected' : '')}><Typography variant='h6' className={classes.lightFont}>{labels.sectionTitles.menu.toUpperCase()}</Typography></button>
        <button onClick={() => section === 'locales' ? handleSectionChange('index') : handleSectionChange('locales')} className={"button" + (section === "locales" ? ' button-selected' : '')}> <Typography variant='h6' className={classes.lightFont} >{labels.sectionTitles.locales.toUpperCase()}</Typography></button>
      </div>
    )
  }

  const renderMenuBar = () => {
    return (
      <div className={"sections-container" + (shouldBackdropNav ? ' sections-container-backdrop-active' : '')}>
        {section !== 'index' && <div>
          <button onClick={() => handleSectionChange('index')} className={"title-button"}><ArrowBackOutlined className="icon-back" alt="volver" /><Typography variant='h4' className={classes.titulo}>Abbiocco</Typography></button>
        </div>
        }
        {
          (section === 'index') ?
            renderMenuOptions(true)
            : <Hidden only='xs'>{renderMenuOptions()}</Hidden>
        }
      </div>
    )
  }

  return (
    <div className="layout-section" onMouseMove={onMouseMove}>
      {section === 'index' && <PresentationImg />}
      {/* <div className="language-selection-div">
        <button onClick={() => setLanguage("es")} disabled={language == "es"} className={"button" + (language == "es" ? ' button-selected' : '')}><Typography variant='body1' className={classes.lightFont}>ESPAÑOL</Typography></button>
        <button onClick={() => setLanguage("en")} disabled={language == "en"} className={"button" + (language == "en" ? ' button-selected' : '')}><Typography variant='body1' className={classes.lightFont}>ENGLISH</Typography></button>
      </div> */}
      {section !== 'menu' && <ContactVerticalBar />}
      <About labels={labels} show={section === 'nosotros'} />
      <Menu show={section === 'menu'} handleScroll={handleScroll} labels={labels} />
      <Locales show={section === 'locales'} handleScroll={handleScroll} labels={labels} />
      {renderMenuBar()}
      <div className='text-container'>
        <Grow in={section === 'index'} unmountOnExit timeout={{ enter: 500, exit: 500 }}>
          <div className='title-container'>
            <div className='title-name-container'>
              <Typography variant='h2' className={classes.titulo}>ABBIOCCO</Typography>
            </div>
            <div className='title-container-separator' />
            <div className='specialities'>
              <Typography variant='h4' className='speciality'>{labels.subtitulo.toUpperCase()}</Typography>
            </div>
          </div>
        </Grow>
      </div>
      {section === 'index' && <Copyright labels={labels} />}
    </div>
  );
}

export default Presentation;

function PresentationImg() {
  return (
    <div className='img-container img-container-animated'>
      <img src={require('../assets/food-fork-pasta.jpg')} className="img" alt="logo" />
      <div className="overlay-gradient" />
    </div>
  )
}

function ContactVerticalBar() {
  const onMailClick = () => {
    window.location.href = "mailto:Abbiocco@fakemail.com";
  }
  const onInstagramClick = () => {
    window.open("https://www.instagram.com/", "_blank")
  }
  const onTwitterClick = () => {
    window.open("https://www.twitter.com", "_blank")
  }
  const onFacebookClick = () => {
    window.open("https://facebook.com", "_blank")
  }

  return (
    <div className="contact-items-vertical-container">
      <div className="item">
        <img src={require('../assets/icon-facebook.png')} className="icon icon-odd" alt="facebook" onClick={onFacebookClick} />
      </div>
      <div className="item">
        <img src={require('../assets/icon-twitter.png')} className="icon icon-even" alt="twitter" onClick={onTwitterClick} />
      </div>
      <div className="item">
        <img src={require('../assets/icon-instagram.png')} className="icon icon-odd" alt="instagram" onClick={onInstagramClick} />
      </div>
      <div className="item">
        <img src={require('../assets/icon-gmail.png')} className="icon icon-even" alt="gmail" onClick={onMailClick} />
      </div>
    </div>
  )
}

function Copyright({ labels }) {
  const onCopyrightClick = () => {
    window.open(labels.copyright.url, "_blank")
  }

  return <div className='copyright'>
    <Typography variant='body2' className='copyright-text' onClick={onCopyrightClick}>{labels.copyright.text.toUpperCase()}</Typography>
  </div>
}