function crearComida(inputComida) {
    const { id, nombre, descripcion, foto, isRecommended } = inputComida
    let comida = {
        id: id || 1,
        nombre: nombre || '',
        descripcion: descripcion || '',
        foto: foto || '',
        isRecommended: isRecommended || false
    }
    return comida
}

