import React, { useState, } from 'react';
import './Helpers.css';
import { Typography  } from '@material-ui/core';


function MensajePaper({mensaje}) {

    return (
        <div className={'mensaje-container'+ (mensaje && mensaje.success ? ' mensaje-exito' : 'mensaje-fail')}>
            <Typography variant='body1' className='mensaje-texto'>{mensaje? mensaje.msg : ''}</Typography>
        </div>
        )
}
export default MensajePaper;