import React, { useState, } from 'react';
import './Section.css';
import './Locales.css';
import LocationElements from '../helpers/LocationElements'
import { Typography, Hidden, Button,TextField,Dialog,DialogActions,DialogContent,DialogContentText,DialogTitle} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import RoomOutlined from '@material-ui/icons/RoomOutlined'
import BookOutlined from '@material-ui/icons/BookOutlined'
import ArrowForwardOutlined from '@material-ui/icons/ArrowForwardOutlined'
import LoaderComponent from '../helpers/LoaderComponent'
import MensajePaper from '../helpers/MensajePaper'

const useStyles = makeStyles(theme => ({
  lightFont: {
    fontWeight: 200,
  },
}));

function Locales({ labels, show, handleScroll }) {
  const classes = useStyles();
  const [indexFocused, setIndexFocused] = useState(null)
  const [isReservando, setIsReservando] = useState(false)
  const [reservaElement, setReservaElement] = useState(null)

  const handleLocationClick = (indexLocationElement) => {
    if (indexFocused === null || indexLocationElement !== indexFocused) setIndexFocused(indexLocationElement)
    else setIndexFocused(null)
  }

  const onReservar = (event,indexLocationElement) => {
    event.stopPropagation()
    setIsReservando(true)
    setReservaElement(LocationElements[indexLocationElement])
  }

  const onReservarClose = () => {setIsReservando(false); setReservaElement(null)}

  return (
    <div className={"container container-projects" + (show ? ' container-show' : '')}>
      <ReservarDialog open={isReservando} onClose={onReservarClose} local={reservaElement} labels={labels}/>
      <div className="container-grid" onScroll={handleScroll} >
        {LocationElements.map((locationElement, indexLocationElement) => {
          return (
            <div className='location-container' key={'locationElement' + indexLocationElement} onClick={() => handleLocationClick(indexLocationElement)}>
              <div className={'location-element-image-container' + (indexLocationElement === indexFocused ? ' location-element-image-show-map' : '')}>
                <img src={locationElement.img} className="location-element-img" alt="location" />
                <div className="location-element-image-overlay-gradient" />
                <Typography variant='h2' align='center' className='location-element-nombre' align='center' >{locationElement.nombre}</Typography>
                <div className='location-element-actions-container'>
                  <button className='button-action' onClick={() => handleLocationClick(indexLocationElement)}>
                    <RoomOutlined className='icon' />
                    <Typography variant='body1' align='center' align='center'>{labels.buttonUbicacion.toUpperCase()}</Typography>
                  </button>
                  <button className='button-action' onClick={(event) => onReservar(event,indexLocationElement)}>
                    <BookOutlined className='icon' />
                    <Typography variant='body1' align='center' align='center'>{labels.buttonReservar.toUpperCase()}</Typography>
                  </button>
                </div>
              </div>
              <div className={'location-element-image-container location-element-map-container' + (indexLocationElement === indexFocused ? ' location-element-map-show-map' : '')}>
                <img src={locationElement.map} className="location-element-map" alt="location" />
                <div className='map-marker-outer'> <div className='map-marker-middle'><div className='map-marker' /></div></div>
                <div className="location-element-map-overlay-gradient" />
                <Typography variant='h5' align='center' className='location-element-address' align='center' >{locationElement.address.toUpperCase()}</Typography>
                <ArrowForwardOutlined className='button-map-volver' fontSize='large' onClick={() => handleLocationClick(indexLocationElement)} />
              </div>
            </div>
          )
        })}
      </div>
    </div>
  );
}

export default Locales;

function ReservarDialog({open, onClose, local, labels}) {
  const [nombre,setNombre] =useState('')
  const [email,setEmail] =useState('')
  const [telefono,setTelefono] =useState('')
  const [cantidad,setCantidad] =useState(1)
  const [notas,setNotas] =useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [message, setMessage] = useState(null)

  const inputs = [
    {label:labels.formReservaInputs.nombre, type:'text',required:true,value:nombre, setter:setNombre, autoFocus:true},
    {label:labels.formReservaInputs.email, type:'text', required:true, value:email, setter:setEmail},
    {label:labels.formReservaInputs.telefono, type:'text',required:true, value:telefono, setter:setTelefono},
    {label:labels.formReservaInputs.cantidad, type:'number',required:true, value:cantidad, setter:setCantidad},
    {label:labels.formReservaInputs.notas, type:'text',required:false, multiline:true, rows:5, value:notas, setter:setNotas}
  ]

  const canReservar = nombre !== '' && email !== '' && telefono !== '' && cantidad > 0

  const onReservar = () => {
    setIsLoading(true)
    setTimeout(function(){ setMessage({success:true, msg:labels.formReservaInputs.msgExito}); setIsLoading(false) }, 3000);
  }

  return (
      <Dialog open={open} onClose={onClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">{labels.reservaDialogTitle}</DialogTitle>
        <DialogContent>
        {message!== null && <MensajePaper  mensaje={message} />}
          <DialogContentText>
            {local ? labels.reservaDialogDescription(local.nombre, local.address) : ''}
          </DialogContentText>
          {inputs.map((input,index) => 
          <TextField
            key={index}
            autoFocus={input.autoFocus || false}
            margin="dense"
            id="name"
            label={input.label}
            type={input.type}
            required={input.required}
            value={input.value}
            onChange={(event) => input.setter(event.target.value)}
            fullWidth
            rows={input.rows || null}
            multiline={input.multiline || false}
          />)}
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose} color="primary">
            {labels.buttonCancelar}
          </Button>
          <Button onClick={onReservar} color="primary" disabled={!canReservar}>
            {labels.buttonReservar}
          </Button>
        </DialogActions>
        {isLoading && <LoaderComponent />}
      </Dialog>
  );
}