function crearLocation(inputLocation) {
    const { id, nombre, descripcion, img, address, map } = inputLocation
    let comida = {
        id: id || 1,
        nombre: nombre || '',
        descripcion: descripcion || '',
        img: img || '',
        address: address || '',
        map: map || ''
    }
    return comida
}

